// 

const state = {
  navWorkflow: []
};

const mutations = {

  // 
  postNavWorkflowNode(state, items) {
    state.navWorkflow.push(items);
  },

  // 
  postNavWorkflowData(state, items) {
    state.navWorkflow = items;
  },

  // 
  getNavWorkflowState() {
    state.navWorkflow = [];
  }
};

export default {
  state,
  mutations
}