// import { message } from 'ant-design-vue';
import { REFRESH_TOKEN } from '../../types';
import projectModels from '../../../server/projectModel';

export default {
  [REFRESH_TOKEN] ({ commit, state }) {
    // Promise
    return new Promise((resolve, reject) => {
      // refresh tokentoken
      projectModels.postTestRefreshModel(state.refreshToken).then(({ data: newToken }) => {
        // tokenvuex
        commit(REFRESH_TOKEN, newToken);
        resolve(newToken);
      }).catch(reject);
    });
  }
};