// import Vue from 'vue';
// 

const state = {
  workflowNodeInfo: []
};

const mutations = {

  // 
  postworkflowNode(state, items) {
    state.workflowNodeInfo.push(items);
  },

  // 
  postworkflowNodeData(state, items) {
    state.workflowNodeInfo = items;
  },

  // 
  getDefaultState() {
    state.workflowNodeInfo = [];
  }
};

export default {
  state,
  mutations
}