import progressBar from './loadingProgress.vue'

export default {
  install (Vue, options = {}) {
    // 
    Vue.component(progressBar.name, progressBar)

    //  Vue 
    const Component = Vue.extend(progressBar)
    // 
    
    const { autoFinish, ...res } = options
  
    // 
    const vm = new Component({
      data: {
        autoFinish: typeof autoFinish === 'boolean' ? autoFinish : true
      }
    })
    //  progressBar  options   options 
    options = Object.assign(vm.$props.options, { ...res })

    // props
    vm.$propsData = options
    vm.$mount()

    // 
    if (!vm.$isServer) {
      document.body.appendChild(vm.$el)
    }

    let timer = null

    const progress = {
      start () {
        if (Vue.$isServer) return
        //  start 0
        vm.percent = 0
        vm.show = true
        vm.canSuccess = true

        // 
        const CUT_SCALE = 5

        //  100 
        timer = setInterval(() => {
          // 
          this.increase((CUT_SCALE - 1) * Math.random() + 1)
          //  95%
          if (vm.percent > 95 && vm.autoFinish) {
            this.finish()
          }
        }, 100)
      },

      increase (cut) {
        vm.percent = Math.min(99, vm.percent + cut)
      },

      hide () {
        clearInterval(timer)
        // 2 
        //  opacity 
        // 01000
        setTimeout(() => {
          vm.show = false
          setTimeout(() => {
            vm.percent = 0
            timer = null
          }, vm.options.transition.opacitySpeed)
        }, vm.options.transition.duration)
      },

      // 2
      finish () {
        if (Vue.$isServer) return
        vm.percent = 100
        this.hide()
      },

      fail () {
        if (Vue.$isServer) return
        // 
        vm.canSuccess = false
        vm.percent = 100
        this.hide()
      }
    }

    // 
    Vue.prototype.$progress = progress
  }
}