<!--
* @componmentName loadingProgress
* @module "src\PublicComponment\loadingProgress\loadingProgress"
* @fileDescription 
* @author li
* @date 2020810
* @param {Object} [tableStyle] 
* @example 
* main.js
* this.$progress.start(); 
* this.$progress.finish(); 
* this.$progress.fail(); 
 -->

<template>
  <div class="loadingProgress">
    <div class="pace pace-inactive">
      <div class="pace-progress" :style="style"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'loadingProgress',

  props: {
    options: {
      type: Object,
      default() {
        return {
          succColor: '#264A9C',
          failColor: '#f20c00',
          position: 'top',
          transition: {
            widthSpeed: 200,
            opacitySpeed: 400,
            duration: 300, //  ms
          },
          inverse: true, // 
          thickness: 2, // 
        };
      },
    },
  },

  data() {
    return {
      percent: 0, // 
      show: false, // 
      canSuccess: true, // 
    };
  },

  computed: {
    style() {
      // 
      const { succColor, failColor, position, transition, inverse, thickness } = this.options;
      const { widthSpeed, opacitySpeed } = transition;
      const { canSuccess, percent, show } = this;

      //  css 
      const style = {
        backgroundColor: canSuccess ? succColor : failColor,
        opacity: show ? 1 : 0,
      };

      if (position !== 'top' && position !== 'bottom') { // 
        throw new Error('The wrong config of position!'); 
      }

      style[position] = 0; // style.top:0style.bottom:0

      if (inverse) {
        style.left = 0;
      } else {
        style.right = 0;
      }

      style.width = percent + '%'; // 
      style.height = thickness + 'px'; // 
      style.transition = `width ${widthSpeed}ms, opacity ${opacitySpeed}ms`; // 

      return style;
    },
  },
};
</script>

<style lang="scss" scoped>
.loadingProgress {
  // display: none;
  .pace .pace-progress {
    position: fixed;
    z-index: 100000;
    opacity: 1;
  }
}
</style>